<template>
    <div class="yt_home">
        <div class="yt_lb">
            <img
            :src="'../../statics/images/yt-'+(index+1)+'.jpg'"
            v-show='isShowImg==index+1'
            v-for="(item,index) in lb_list"             
            />
        </div>
        <h1></h1>
        <ul>
            <li v-for="item in list" class="clear_float">
                <div class="list_left">
                    <img :src=item.img alt="">
                    <!-- <video src=""></video> -->
                </div>
                <div class="list_right">
                    <h2>{{item.title}}</h2>
                    <h4>:{{item.time}}</h4>
                    <h4>:{{item.address}}</h4>
                    <p>
                        <span>
                            {{item.title}}:
                        </span>
                        {{item.intro}}
                    </p>
                </div>
            </li>
        </ul>
        <p></p>
    </div>
</template>

<script>
export default {
    data(){
        return {
            list:[
                {
                    title:'',
                    time:'2018325',
                    address:'',
                    intro:`
                            
                            
                            
                            
                            
                            
                            
                            
                            
                            
                            
                            `,
                    img:'../../statics/images/yt-'+1+'.jpg'
                },
                {
                    title:'',
                    time:'2018325',
                    address:'',
                    intro:`
                            
                            
                            
                            
                            
                            
                            
                            
                            
                            
                            
                            ,
                            
                            
                            ( )
                            
                            
                            
                            
                            ,`,
                    img:'../../statics/images/yt-'+2+'.jpg'
                },
                {
                    title:'',
                    time:'2018325',
                    address:'',
                    intro:`
                            

                            
                            

                            
                            
                            
                            

                            
                            `,
                    img:'../../statics/images/yt-'+3+'.jpg'
                },
                {
                    title:'',
                    time:'2018325',
                    address:'',
                    intro:`
                            
                            
                            
                            
                            
                            
                            
                            
                            
                            
                            
                            `,
                    img:'../../statics/images/yt-'+4+'.jpg'
                },
                {
                    title:'',
                    time:'2018325',
                    address:'',
                    intro:`
                            
                            
                            
                            
                            
                            
                            
                            
                            
                            
                            
                            `,
                    img:'../../statics/images/yt-'+5+'.jpg'
                }
            ],
            isShowImg:1,
            lb_list:[1,2,3,4,5]
        }
    },
    mounted(){
        setInterval(()=>{
            // console.log(this.isShowImg);
            this.isShowImg>this.lb_list.length-1?this.isShowImg=1:this.isShowImg+=1;
        },3000);
    }
}
</script>




